import styled from "styled-components";
import { css } from "styled-components";
import { useState } from "react";

import form_phone from "../../assets/images/Elements/form_phone.png";

import { A, Text, Title3 } from "../../assets/styles/component";
import CheckBox from "./CheckBoxMobile";
import Button from "../../assets/UI/ButtonMobile";

import { Telegram } from "../../assets/images/Socials/Telegram";

import { EngLoc } from "../../assets/constants/localizations";

const SForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3.75rem 1rem;
  position: relative;
  overflow: hidden;
  gap: 4.875rem;

  @media (min-width: 600px) {
    padding: 3.75rem 5%;
  }

  & button {
    background: #ffffff;
    border-radius: 7.813rem;
    width: 100%;
    padding: 1.563rem 3.188rem;
  }

  background: radial-gradient(
      43.18% 87.18% at 67.39% 25.75%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    radial-gradient(
      25.12% 79.02% at 32.93% 125.8%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      38.5% 124.42% at 61.09% 172.86%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      60.8% 152.7% at -13.59% 11.65%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      64.16% 160.68% at 112.44% 38.89%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      26.08% 44.57% at 5.86% 120.25%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    #000f67;

  img {
    width: 90%;

    max-width: 29.125rem;
    z-index: 100;
    transform: translateY(3.75rem);
  }
`;

const FormBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2.188rem;

  width: 100%;

  text-align: left;
  z-index: 2;
`;

const Div = css`
  display: flex;
  flex-direction: column;
`;

const TitleBox = styled.div`
  ${Div}
  gap: 1.563rem;

  @media (max-width: 500px) {
    max-width: 14.188rem;
  }
`;

const Title = styled(Title3)`
  font-weight: 400;
  font-size: 2.375rem;
  line-height: 110%;

  @media (max-width: 500px) {
    max-width: 14.063rem;
  }
`;

const P = styled(Text)`
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 150%;
`;

const InputsBox = styled.div`
  position: relative;

  display: flex;
  flex-direction: column;
  gap: 0.875rem;

  width: 100%;

  & svg {
    position: absolute;
    opacity: 0.3;
    top: 1.25rem;
    left: 190px;
  }
`;

const Fields = css`
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 0.938rem;

  max-width: 500px;
  height: 4.375rem;

  padding-left: 1.5rem;
  outline: none;
  box-sizing: border-box;

  &::placeholder,
  & {
    font-family: "HeliosExtC", -apple-system, BlinkMacSystemFont, "Segoe UI",
      "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 150%;
    color: #fff;
  }

  &:focus {
    outline: 2px solid rgba(255, 255, 255, 0.3);
  }
`;

const Input = styled.input`
  ${Fields}
  width: 100%;
`;

const TextAreaBox = styled.div`
  position: relative;
  width: 100%;

  & p {
    position: absolute;
    top: 1.625rem;
    left: 159px;
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 150%;
    color: #ffffff66;
  }
`;

const Textarea = styled.textarea`
  ${Fields}
  width: 100%;
  height: 212px;
  padding-top: 1.563rem;

  resize: none;
  position: relative;
`;

const ToggleBox = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1rem;
`;

const Link = styled(A)`
  color: #fff;
  text-decoration: underline;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 160%;

  &:hover {
    color: #abe4f7;
  }
`;

const ButtonBox = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2rem;

  width: 100%;
  text-align: left;

  & p {
    max-width: 346px;
    font-weight: 400;
    font-size: 0.75rem;
    line-height: 160%;
    color: #ffffff99;
  }

  & button {
    box-sizing: border-box;
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.063rem;
  }
`;

const RequestForm = ({ language }) => {
  const [value, setValue] = useState("");
  const [valueTextArea, setValueTextArea] = useState("");

  return (
    <SForm id={"form"}>
      <FormBox>
        <TitleBox>
          <Title>
            {language === "Ru"
              ? "Хотите запустить рекламу?"
              : EngLoc.social_media.form.title}
          </Title>
        </TitleBox>

        <InputsBox>
          {value.length === 0 && <Telegram />}
          <Input
            placeholder={
              language === "Ru"
                ? "Ваш номер или ник"
                : EngLoc.social_media.form.input
            }
            onFocus={(e) => {
              e.target.value == "" && setValue(" ");
              e.target.selectionStart = 0;
              e.target.selectionEnd = 0;
            }}
            onBlur={(e) => {
              e.target.value == " " && setValue("");
            }}
            onChange={(e) => setValue(e.target.value)}
            value={value}
          />

          <TextAreaBox>
            {valueTextArea.length === 0 && (
              <Text>
                {language === "Ru"
                  ? "(не обязательно)"
                  : EngLoc.social_media.form.textarea_after}
              </Text>
            )}

            <Textarea
              placeholder={
                language === "Ru"
                  ? "Опишите задачу"
                  : EngLoc.social_media.form.textarea
              }
              onFocus={(e) => {
                e.target.value == "" && setValueTextArea(" ");
                e.target.selectionStart = 0;
                e.target.selectionEnd = 0;
              }}
              onBlur={(e) => {
                e.target.value == " " && setValueTextArea("");
              }}
              onChange={(e) => setValueTextArea(e.target.value)}
              value={valueTextArea}
            ></Textarea>
          </TextAreaBox>
        </InputsBox>

        <ButtonBox>
          {language === "Ru" ? (
            <Text size="12">
              Нажимая на кнопку, вы даете согласие на обработку{" "}
              <Link
                targer="_blank"
                href="/privacy"
                aria-label={"Прочитать политику обработки персональных данных"}
              >
                персональных данных
              </Link>{" "}
              и соглашаетесь c{" "}
              <Link
                targer="_blank"
                href="/terms"
                aria-label={"Прочитать политику конфиденциальности"}
              >
                политикой конфиденциальности
              </Link>
            </Text>
          ) : (
            <Text size="12">
              By clicking on the button, you consent to the processing of
              <Link
                targer="_blank"
                href="/privacy"
                aria-label={"Прочитать политику обработки персональных данных"}
              >
                personal data
              </Link>{" "}
              and agree to
              <Link
                targer="_blank"
                href="/terms"
                aria-label={"Прочитать политику конфиденциальности"}
              >
                {" "}
                the privacy policy
              </Link>
            </Text>
          )}
          <Button type="secondary" colorTxt="#2850BF">
            {language === "Ru" ? "Запустить рекламу" : EngLoc.buttons.send}
          </Button>
        </ButtonBox>
      </FormBox>
    </SForm>
  );
};

export default RequestForm;
