import styled from "styled-components";

import { RBK } from "../../assets/images/Brands/RBK";
import AdIndex from "../../assets/images/Elements/logo-adindex.png";
import CoSSa from "../../assets/images/Elements/logo-cossa.png";
import GazetaRu from "../../assets/images/Elements/logo-gazetaru.png";
import Kommersant from "../../assets/images/Elements/logo-kommersant.png";
import RBru from "../../assets/images/Elements/logo-rbru.png";
import Sostav from "../../assets/images/Elements/logo-sostav.png";
import Vedomosty from "../../assets/images/Elements/logo-vedomosty.png";

import { Text } from "../../assets/styles/component";

const SBrandsList = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.188rem;

  margin: 0 1rem;
  margin-bottom: 3.75rem;

  @media (min-width: 600px) {
    margin: 0 4%;
    margin-bottom: 3.75rem;
  }

  will-change: scroll-position;
`;

const LogosContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
  align-items: center;

  gap: 0.625rem;

  & p {
    color: #000;
    max-width: 238px;
    font-size: 1.25rem;
    margin-bottom: 0.625rem;
  }
`;

const LogoBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;

  width: calc(50% - 0.625rem);
  max-width: 220px;
  height: 4.5rem;

  background: rgba(205, 205, 205, 0.15);
  border: 2px solid rgba(241, 249, 255, 0.25);
  border-radius: 1.063rem;

  & svg,
  & img {
    max-width: 70%;
    max-height: 40%;
  }
`;

const Public = styled.div`
  width: 100%;

  & p {
    color: #000;
    font-weight: 400;
    font-size: 0.875rem;
    line-height: 150%;
  }
`;

const Email = styled.a`
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 150%;

  color: #126bd1;
  text-decoration: underline;
`;

const LogoImg = styled.img`
  filter: grayscale(1);
  opacity: .6;
  max-width: 80%;
  max-height: 80%;
`;

const BrandsList = () => {
  const Logos = [
    <RBK />,
    <LogoImg src={AdIndex} loading="lazy" />,
    <LogoImg src={CoSSa} loading="lazy" />,
    <LogoImg src={GazetaRu} loading="lazy" />,
    <LogoImg src={Kommersant} loading="lazy" />,
    <LogoImg src={RBru} loading="lazy" />,
    <LogoImg src={Sostav} loading="lazy" />,
    <LogoImg src={Vedomosty} loading="lazy" />,
  ];

  return (
    <SBrandsList>
      <LogosContainer>
        {Logos.map((logo, index) => (
          <LogoBox key={index}>{logo}</LogoBox>
        ))}
      </LogosContainer>
      <Public>
        <Text>Хотите опубликовать о нас материал?</Text>
        <Email
          href="mailto:ads@socialjet.ru"
          aria-label={"Написать нам на почту"}
        >
          ads@socialjet.ru
        </Email>
      </Public>
    </SBrandsList>
  );
};

export default BrandsList;
