import styled from "styled-components";
import { Text } from "../../assets/styles/component";

const SCheckBox = styled.label`
  position: relative;
  display: flex;
  align-items: center;
  gap: 1.25rem;
  height: 1.875rem;
  max-width: 18.75rem;

  padding-left: 3.125rem;

  font-size: 1.375rem;
  user-select: none;
  cursor: pointer;

  & input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  & span {
    position: absolute;
    top: 0;
    left: 0;
    height: 1.875rem;
    width: 1.875rem;
    background: #ffffff22;
    border-radius: 7px;
  }

  span:after {
    content: "";
    position: absolute;
    opacity: 0;
    transition: 0.1s;
  }

  & input:checked ~ span:after {
    opacity: 1;
  }

  & span:after {
    left: 7px;
    top: 7px;
    width: 0.938rem;
    height: 0.938rem;

    background: linear-gradient(290.71deg, #3cc5ff -23.07%, #ffa4f6 147.25%);
    border-radius: 0.313rem;
  }
`;

const SText = styled(Text)`
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 150%;
`;

const CheckBox = ({ name, text }) => {
  return (
    <SCheckBox>
      <SText>{text}</SText>
      <input name="name" type="checkbox" />
      <span></span>
    </SCheckBox>
  );
};

export default CheckBox;
